<template>
  <div class="sequencer">
    <div class="transport">
      <div style="float: right">
        <button @click="clearPart">Clear part</button>
        <button @click="clearPattern">Clear pattern</button>
      </div>
      <button @click="stop" :class="{ 'selected': !isPlaying && !isPaused }">Stop</button>
      <button @click="play" :class="{ 'selected': isPlaying }">Play</button>
      <button @click="pause" :class="{ 'selected': isPaused }">Pause</button>
    </div>
    <div class="temposteps">
      <label class="tempo">Tempo
       <knob-control
          :min="20"
          :max="300" 
          :stepSize="1" 
          v-model="tempo" 
          primary-color="#FF8552"
        ></knob-control>      
      </label>
      <div class="steps" v-if="ready">
        <div 
          v-for="i in numSteps" 
          class="step" 
          :class="{ 'active': currentStep == i, 'on': channels[selectedChannel][i - 1] }" 
          :key="i" 
          @click="toggleStep(i)"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
import KnobControl from 'vue-knob-control'
export default {
  name: 'Sequencer',
  props: ['numSteps', 'numChannels', 'selectedChannel'],
  components: {
    KnobControl
  },
  data() {
    return {
      interval: null,
      timeout: null,
      currentStep: 0,
      channels: [],
      ready: false,
      isPlaying: false,
      isPaused: false,
      tempo: 130
    }
  },
  mounted() {
    this.clearPattern();
    this.ready = true;
  },
  computed: {
    stepLengthMs() {
      return ((60 * 1000) / this.tempo) / 4;
    }
  },
  methods: {
    clearPart() {
      this.channels[this.selectedChannel] = Array(this.numSteps).fill(false);
      this.$forceUpdate(); // why?!
    },
    clearPattern() {
      this.channels = [];
      for(let i = 0; i < this.numChannels; i++) {
        this.channels.push(Array(this.numSteps).fill(false));
      }
      this.$forceUpdate(); // why?!
    },
    toggleStep(i) {
      this.channels[this.selectedChannel][i - 1] = !this.channels[this.selectedChannel][i - 1];
      this.$forceUpdate(); // why?!
    },
    stop() {
      this.isPlaying = false;
      this.isPaused = false;     
      clearTimeout(this.timeout);
      this.currentStep = 0;
    },
    checkCurrentStepForEvents() {
        for(let i = 0; i < this.numChannels; i++) {
          if(this.channels[i][this.currentStep - 1]) {
            this.$emit('noteOn', i);
          }
        }
    },
    play() {

      if(this.isPlaying) {
        return false;
      }

      this.isPlaying = true;
      this.isPaused = false;

      if(!this.currentStep) {
        this.currentStep = 1;
      }
      this.checkCurrentStepForEvents();

      this.timeout = window.setTimeout(() => {
        this.step();
      }, 
      this.stepLengthMs);

    },
    pause() {
      this.isPlaying = false;
      this.isPaused = true;    
      clearTimeout(this.timeout);
    },
    setselectedChannel(i) {
      this.selectedChannel = i;
    },
    step() {
        this.currentStep++;
        if(this.currentStep > 16) {
          this.currentStep = 1;
        }  
        this.checkCurrentStepForEvents();
        this.timeout = window.setTimeout(() => {
          this.step();
        }, 
        this.stepLengthMs);
    }
  }  
}
</script>
<style lang="scss" scoped>
.transport {
  margin: 16px 0;

  button {
    margin-right: 8px;
  }    
}

.temposteps {

  display: grid;
  grid-gap: 16px;
  grid-template-columns: auto 1fr;

  .tempo {
    text-align: center;
  }

  .steps {
    display: grid;
    grid-gap: 8px;
    grid-template-columns: repeat(16, 1fr);

    .step {
      padding-top: 150%;
      background: #999;
      border-radius: 4px;

      &.active {
        background: lighten(#999, 15%);
      }

      &.on {
        background: #FF8552;

        &.active {
          background: lighten(#FF8552, 15%);
        }      
      }

    }  
  }  
}


</style>