<template>
  <div class="drums">

    <fieldset>
      <legend>Kick</legend>

      <label>Decay
        <knob-control
          :min="0.1"
          :max="8" 
          :stepSize="0.1" 
          v-model="kick.decay" 
          :value-display-function="oneDecimalPlace" 
          primary-color="#FF8552"
        ></knob-control>        
      </label>
      <label>Tune
        <knob-control
          :min="40"
          :max="140" 
          :stepSize="1" 
          v-model="kick.baseFreq" 
          primary-color="#FF8552"
        ></knob-control>           
      </label>
      <label>Pitch
        <knob-control
          :min="0"
          :max="500" 
          :stepSize="10" 
          v-model="kick.pitch" 
          primary-color="#FF8552"
        ></knob-control>         
      </label>
      <label>Pitch Decay
       <knob-control
          :min="0.05"
          :max="0.75" 
          :stepSize="0.025" 
          v-model="kick.pitchDecay" 
          :value-display-function="threeDecimalPlaces"
          primary-color="#FF8552"
        ></knob-control>       
      </label>      
      <label>Volume
       <knob-control
          :min="0"
          :max="1" 
          :stepSize="0.01" 
          v-model="kick.volume" 
          :value-display-function="twoDecimalPlaces"
          primary-color="#FF8552"
        ></knob-control>       
      </label>      
      <button @click="playKick">Trigger</button>
      <button @click="setSequencerActiveChannel(0)" :class="{ 'selected': selectedChannel == 0 }">Select</button> 
    </fieldset>

    <fieldset>
      <legend>Hat</legend>

      <label>Decay
        <knob-control
          :min="0.1"
          :max="2" 
          :stepSize="0.1" 
          v-model="hat.decay" 
          :value-display-function="oneDecimalPlace"
          primary-color="#FF8552"
        ></knob-control>
      </label>
      <label>Cutoff
        <knob-control
          :min="3000"
          :max="15000" 
          :stepSize="100" 
          v-model="hat.cutoff" 
          primary-color="#FF8552"
        ></knob-control>        
      </label>  
      <label>Volume
       <knob-control
          :min="0"
          :max="1" 
          :stepSize="0.01" 
          v-model="hat.volume" 
          :value-display-function="twoDecimalPlaces"
          primary-color="#FF8552"
        ></knob-control>       
      </label>
      <button @click="playHat">Trigger</button>
      <button @click="setSequencerActiveChannel(1)" :class="{ 'selected': selectedChannel == 1 }">Select</button> 
    </fieldset>

    <fieldset>
      <legend>Snare</legend>

      <label>Noise Decay
        <knob-control
          :min="0.1"
          :max="8" 
          :stepSize="0.1" 
          v-model="snare.noiseDecay" 
          :value-display-function="oneDecimalPlace"
          primary-color="#FF8552"
        ></knob-control>  
      </label>
      <label>Noise Amp
        <knob-control
          :min="0.1"
          :max="1" 
          :stepSize="0.1" 
          v-model="snare.noiseAmplitude" 
          :value-display-function="oneDecimalPlace"
          primary-color="#FF8552"
        ></knob-control>          
      </label>      
      <label>Tune
        <knob-control
          :min="100"
          :max="600" 
          :stepSize="1" 
          v-model="snare.toneFreq" 
          primary-color="#FF8552"
        ></knob-control>           
      </label>  
      <label>Tone Decay
        <knob-control
          :min="0.1"
          :max="8" 
          :stepSize="0.1" 
          v-model="snare.toneDecay" 
          :value-display-function="oneDecimalPlace"
          primary-color="#FF8552"
        ></knob-control>
      </label>
      <label>Tone Amp
        <knob-control
          :min="0.1"
          :max="1" 
          :stepSize="0.1" 
          v-model="snare.toneAmplitude" 
          :value-display-function="oneDecimalPlace"
          primary-color="#FF8552"
        ></knob-control>
      </label>    
      <label>Cutoff
        <knob-control
          :min="200"
          :max="10000" 
          :stepSize="100" 
          v-model="snare.cutoff" 
          primary-color="#FF8552"
        ></knob-control>        
      </label>   
      <label>Volume
       <knob-control
          :min="0"
          :max="1" 
          :stepSize="0.01" 
          v-model="snare.volume" 
          :value-display-function="twoDecimalPlaces"
          primary-color="#FF8552"
        ></knob-control>       
      </label>
      <button @click="playSnare">Trigger</button>
      <button @click="setSequencerActiveChannel(2)" :class="{ 'selected': selectedChannel == 2 }">Select</button> 
    </fieldset>

    <Sequencer 
      :numSteps="16" 
      :numChannels="3" 
      ref="sequencer" 
      @noteOn="noteOn" 
      :selectedChannel="selectedChannel"
    ></Sequencer>

  </div>
</template>

<script>
import KnobControl from 'vue-knob-control'
import Sequencer from './Sequencer';

export default {
  name: 'Drums',
  props: [],
  components: {
    KnobControl,
    Sequencer
  },
  data() {
    return {
      initialised: false,
      audioCtx: null,
      noiseBuffer: null,
      kick: {
        decay: 1, // seconds
        baseFreq: 50, // hz (49-71)
        pitch: 200, // +hz
        pitchDecay: 0.1,  // seconds   
        volume: 0.75,
      },
      hat: {
        decay: 0.3,
        cutoff: 8000,
        volume: 0.75,
      },
      snare: {
        noiseDecay: 0.5, // seconds
        toneFreq: 250, // hz (229-356)
        toneDecay: 0.2,
        noiseAmplitude: 1,
        toneAmplitude: 1,
        cutoff: 3000,
        volume: 0.75  
      },
      selectedChannel: 0
    }
  },
  mounted() {
    var self = this;
    window.addEventListener("keypress", function(e) {
      const key = String.fromCharCode(e.keyCode);
          switch(key) {
            case 'k':
              self.playKick();
              break;
            case 's':
              self.playSnare();
              break;
            case 'h':
              self.playHat();
              break;
          }
      })
  },
  methods: {
    noteOn(i) {
      switch(i) {
        case 0:
          this.playKick();
          break;
        case 1:
          this.playHat();
          break;
        case 2:
          this.playSnare();
          break;
      }
    },
    setSequencerActiveChannel(i) {
      this.selectedChannel = i;
    },
    oneDecimalPlace(val) {
      return val.toFixed(1);
    },
    twoDecimalPlaces(val) {
      return val.toFixed(2);
    },    
    threeDecimalPlaces(val) {
      return val.toFixed(3);
    },    
    init() {

      this.audioCtx = new AudioContext();

      const bufferSize = this.audioCtx.sampleRate * 2;
      this.noiseBuffer = this.audioCtx.createBuffer(1, bufferSize, this.audioCtx.sampleRate);

      let data = this.noiseBuffer.getChannelData(0); // get data

      // fill the buffer with noise
      for (let i = 0; i < bufferSize; i++) {
          data[i] = Math.random() * 2 - 1;
      } 

      this.initialised = true
    },
    playKick() {

      if(!this.initialised) {
        this.init();
      }

      const vco = this.audioCtx.createOscillator();
      vco.type = 'triangle';
      vco.frequency.value = parseInt(this.kick.baseFreq) + parseInt(this.kick.pitch);
      
      vco.frequency.exponentialRampToValueAtTime(parseInt(this.kick.baseFreq), this.audioCtx.currentTime + parseFloat(this.kick.pitchDecay));

      const vca = this.audioCtx.createGain();
      vca.gain.value = 0;

      vco.connect(vca);
      vca.connect(this.audioCtx.destination);
      vco.start(0);

      
      vca.gain.linearRampToValueAtTime(parseFloat(this.kick.volume), this.audioCtx.currentTime + 0.01);
      vca.gain.exponentialRampToValueAtTime(0.01, this.audioCtx.currentTime + parseFloat(this.kick.decay));

      vco.stop(this.audioCtx.currentTime + parseFloat(this.kick.decay));
    },
    playSnare() {

      if(!this.initialised) {
        this.init();
      }     

      // noise
      let noise = this.audioCtx.createBufferSource();
      noise.buffer = this.noiseBuffer;

      let filter = this.audioCtx.createBiquadFilter();
      filter.type = 'bandpass';
      filter.frequency.value = parseInt(this.snare.cutoff);   
      noise.connect(filter);  

      const vca1 = this.audioCtx.createGain();
      vca1.gain.value = 0;  
      filter.connect(vca1);
      vca1.connect(this.audioCtx.destination);

      noise.start();
      vca1.gain.linearRampToValueAtTime(parseFloat(this.snare.noiseAmplitude) * parseFloat(this.snare.volume), this.audioCtx.currentTime + 0.01);
      vca1.gain.exponentialRampToValueAtTime(0.01, this.audioCtx.currentTime + parseFloat(this.snare.noiseDecay));
      noise.stop(this.audioCtx.currentTime + parseFloat(this.snare.noiseDecay));

      // tone
      const vco = this.audioCtx.createOscillator();
      vco.type = 'triangle';
      vco.frequency.value = parseInt(this.snare.toneFreq);

      const vca2 = this.audioCtx.createGain();
      vca2.gain.value = 0;  
      vco.connect(vca2);
      vca2.connect(this.audioCtx.destination);

      vco.start();
      vca2.gain.linearRampToValueAtTime(parseFloat(this.snare.toneAmplitude) * parseFloat(this.snare.volume), this.audioCtx.currentTime + 0.01);
      vca2.gain.exponentialRampToValueAtTime(0.01, this.audioCtx.currentTime + parseFloat(this.snare.toneDecay));
      vco.stop(this.audioCtx.currentTime + parseFloat(this.snare.toneDecay));

    },
    playHat() {

      if(!this.initialised) {
        this.init();
      } 

      // noise
      let noise = this.audioCtx.createBufferSource();
      noise.buffer = this.noiseBuffer;

      let filter = this.audioCtx.createBiquadFilter();
      filter.type = 'highpass';
      filter.frequency.value = parseInt(this.hat.cutoff);   
      noise.connect(filter);

      const vca = this.audioCtx.createGain();
      vca.gain.value = 0; 
      filter.connect(vca);
      vca.connect(this.audioCtx.destination);

      noise.start();
      vca.gain.linearRampToValueAtTime(parseFloat(this.hat.volume), this.audioCtx.currentTime + 0.01);
      vca.gain.exponentialRampToValueAtTime(0.01, this.audioCtx.currentTime + parseFloat(this.hat.decay));
      noise.stop(this.audioCtx.currentTime + parseFloat(this.hat.decay));
    }
  }  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
fieldset {
  margin-bottom: 8px;
}

label {
  text-align: center;
  float: left;
  margin-right: 16px;  
}

button {
  margin-right: 8px;
}
</style>
